html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Muller", sans-serif;
  min-width: 320px;
  min-height: 100%;
  color: #121723;
}

.container {
  margin: 0 auto;
  max-width: 1110px;
  width: 100%;
  box-sizing: border-box;
}
@media (max-width: 1440px) {
  .container {
    max-width: 824px;
  }
}
@media (max-width: 1023px) {
